import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";
import { Doughnut } from "react-chartjs-2";

// Chart.js  
ChartJS.register(ArcElement, Tooltip, Legend);

const Chart = (props) => {
  const text = `${props.value}`; //   
  const isNan = text === "x";
  const number = isNan ? 0 : parseInt(text.slice(0, -1)); // %    

  const textCenter = {
    id: "textCenter",
    beforeDraw(chart) {
      const { width } = chart;
      const { height } = chart;
      const ctx = chart.ctx;
      ctx.restore();

      const fontSize = (height / 100).toFixed(2); //      
      ctx.font = `bold ${fontSize}em sans-serif`;
      ctx.fillStyle = "white";
      ctx.textBaseline = "middle";

      const textX = Math.round((width - ctx.measureText(text).width) / 2); //  
      const textY = height / 2; //  

      ctx.fillText(text, textX, textY);
      ctx.save();
    },
  };

  const data = {
    labels: ["Blue", "Grey"],
    datasets: [
      {
        label: ["blue", "grey"],
        data: [number, 100 - number],
        backgroundColor: ["rgba(25, 24, 246, 0.8)", "rgba(147, 147, 147, 0.6)"],
        borderWidth: 0.5,
        borderColor: ["rgba(1, 148, 248, 0.75)", "#333333"],
      },
    ],
  };

  const options = {
    cutout: "85%", //     (: 50%)
    plugins: {
      legend: { display: false },
    },
  };

  return <Doughnut data={data} options={options} plugins={[textCenter]} />;
};

export default Chart;
