import { React, useCallback, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useParams } from "react-router-dom";
import { setStats, resetStats } from "../redux/slice/statsSlice";
import NotFoundPage from "./NotFoundPage";
import FieldPlayer from "../component/FieldPlayer";
import Goalkeeper from "../component/Goalkeeper";
import Loading from "./Loading";

const PlayerStatPage = () => {
  const seasonInfo = useSelector((state) => state.seasonInfo);
  const stats = useSelector((state) => state.stats);
  const dispatch = useDispatch();
  const { season, name } = useParams();

  const apiUrl = "https://api.chelseafcplayer.com"; //  API  URL
  // const apiUrl = "http://localhost:3001"; //    localhost URL

  useEffect(() => {
    //        
    dispatch(resetStats());
  }, [name, season, dispatch]);

  //         
  const playerData = seasonInfo
    .find((seasonArray) =>
      seasonArray.some((player) => player.season === parseInt(season))
    )
    ?.find((player) => player.url_name === name);

  //   
  const fetchStats = useCallback(
    async (playerData) => {
      try {
        const response = await fetch(
          `${apiUrl}/player/${season}/${name}/crawl`,
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({ playerData }),
          }
        );

        if (response.status === 404) {
          alert("Player not found on Chelsea roster");
          return;
        }

        if (!response.ok) {
          throw new Error("Failed to fetch stats");
        }

        // /crawl  
        const data = await response.json();
        dispatch(setStats(data));
      } catch (error) {
        console.error("Error fetching stats:", error);
      }
    },
    [dispatch, name, season]
  );

  //          
  useEffect(() => {
    if (playerData && playerData.isChelsea) {
      fetchStats(playerData);
    }
  }, [playerData, fetchStats]);

  //      404  
  if (!playerData || !playerData.isChelsea) {
    return <NotFoundPage />;
  }

  //    Loading UI,       
  return (
    <main className="flex">
      {!stats ? (
        <Loading />
      ) : stats.role !== 0 ? (
        <FieldPlayer />
      ) : (
        <Goalkeeper />
      )}
    </main>
  );
};

export default PlayerStatPage;
